import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

const DirectoryView = (props) => (
    <div>
        <SearchForm searchVal={props.searchVal} handleChange={props.handleChange} />
        {props.recipes.map((recipe, i) => <RecipeCard name={recipe.name}
        key={i} />)}

       
    </div>
);

const SearchForm = (props) => (
    <form>
        <input value={props.searchVal} onChange={props.handleChange} />
        <button>Search</button>
    </form>
);

const RecipeCard = (props) => (
    <div>
        <p>{props.name}</p>
    </div>
);

const DetailView = props => (
    <article>
      <h1>{props.detailRecipe.name.toUpperCase()}</h1>
      <div className={'details'}>
        <div id={'ingredients'}>
          <h5>INGREDIENTS</h5>
          <ul>
            {props.detailRecipe.ingredients.map(ingredient => (
              <li>
                <p>{ingredient}</p>
              </li>
            ))}
          </ul>
        </div>
        <div id={'instructions'}>
          <h5>INSTRUCTIONS</h5>
          <ul>
            {props.detailRecipe.instructions.map(instruction => (
                <li>
                  <p>{instruction}</p>
                </li>
            ))}
          </ul>
        </div>
      </div>
    </article>
  );

